import React from "react";
import cx from "classnames";

import styles from "./diagonal.scss";

export default ({ className, direction = "top", ...rest }) => {
  return (
    <svg
      className={cx(styles.wrap, styles[direction], className)}
      viewBox="0 0 200 20"
    >
      <polygon points="0,0 200,0 0,20"></polygon>
    </svg>
  );
};
